@import '../../../../index.scss';

.listItem {
    cursor: pointer;
    height: 25%;
    width: 340px;
    flex: 1 1 0%;
    background: #212F57;
    padding-left: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-bottom: 1px solid #374365;
    transition: all 0.2s ease-out 0s, opacity 0.2s 0s;
    .listLink {
        text-decoration: none;
        color: #FFF;
        font-family: $Raleway;
        font-size: 1.4em;
        font-weight: bold;
    }
    .subLink {
        font-family: $Lato;
        color: #9BA1B2;
        margin-block-start: 0em;
        margin-block-end: 0em;
    }
}

.listItemActive {
    height: 25%;
    cursor: pointer;
    width: 340px;
    flex: 1 1 0%;
    background: #1C2849;
    padding-left: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-bottom: 1px solid #374365;
    transition: all 0.2s ease-out 0s, opacity 0.2s 0s;
    .listLink {
        text-decoration: none;
        color: #E2786C;
        font-family: $Raleway;
        font-size: 1.4em;
        font-weight: bold;
    }
    .subLink {
        font-family: $Lato;
        color: #FFF;
        margin-block-start: 0em;
        margin-block-end: 0em;
    }
}